<template>
    <div>
        <v-snackbar
            v-model="snackbar"
            top
            right
            :color="color"
            >
            {{msg}}
            <v-btn
            color="black"
            text
            @click="snackbar = false"
            >
            
            </v-btn>
  
            </v-snackbar>
        <v-expansion-panels v-model="openedPanel">
    <v-expansion-panel>
      <v-expansion-panel-header>
        <v-card-title class="text-center" style="display: block;">{{title}}</v-card-title>
      
      </v-expansion-panel-header>
      <v-expansion-panel-content>
        <v-form  >
            <v-row>
                <v-col cols="12" md="4">
             <v-text-field
              v-model="statusData._statusName"
              label="status Name"
              
              outlined
              dense
              hide-details
            ></v-text-field>
                </v-col>
                <v-col cols="12" md="4">
             <v-text-field
              v-model="statusData._statusDate"
              label="Date"
              readonly
              outlined
              dense
              hide-details
            ></v-text-field>
                </v-col> 
                <v-col cols="12" md="4" v-if="edited">
                  <v-combobox
          v-model="statusData._Status"
          :items="status_"
          label="Status"
          clearable
          dense
        ></v-combobox>
                </v-col>
                <v-col cols="12" md="4">
            <v-btn color="primary" type="button" v-if="!edited" @click="saveServicestatus">
              Submit
            </v-btn>
            <!-- updateservice -->
            <v-btn color="info" type="button" v-if="edited" @click="editupload" >
              Update
            </v-btn>
            <v-btn
              type="button"
              @click="FormReset"
              class="mx-2"
            >
              Reset
            </v-btn>
          </v-col>
            </v-row>
        </v-form>
      
      </v-expansion-panel-content>
    </v-expansion-panel>
  </v-expansion-panels>
<v-card>
  <v-card-title>
        <span class="text-center ">Service Status</span>
        <v-spacer></v-spacer>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-card-title>

      <v-data-table
         item-key="id"
        :headers="header_"
        :items="status_category"
        :search="search"
        
      >  
      <template v-slot:[`item.actions`]="{item}" >
          <div >
            <v-btn color="success" class="mx-2" @click="editcategory_(item)" :disabled="isAdmin != 'Administrator'" fab x-small outlined :title="item">
              <i class="fa fa-pencil" aria-hidden="true"></i>
            </v-btn>
            <v-btn :disabled="isAdmin != 'Administrator'" color="red" fab x-small  @click="deleteServiceCategory(item._id)"  outlined>
              <i class="fa fa-trash" aria-hidden="true"></i>
            </v-btn>
            <v-btn :disabled="isAdmin != 'Administrator'" color="info" class="mx-2" fab x-small  outlined  >
              <i class="fa fa-eye" aria-hidden="true"></i>
            </v-btn>
            
           
          </div>
         
           
          </template>
          <template v-slot:no-results>
              <v-alert :value="true" color="danger"  dark>
                Your Search {{search}} does not exist.
              </v-alert>
          </template>
      </v-data-table>
    </v-card>
    </div>
</template>
<script>
import axios from 'axios'
import { onMounted,ref,defineComponent,reactive } from '@vue/composition-api'

export default defineComponent({
    setup() {
        onMounted(() => {
           setDate()
           status_fetch()
           
        })
        let title = ref('Add Service status')
        let _statusDate = ref('')
        let _statusName = ref('')
        let _Status = ref('')
        let E_id = ref('')
        let openedPanel = ref(null)
        let status_ = ref([0,1])

        let snackbar = ref(false)
        let color = ref('')
        let msg = ref('')

        let statusData = reactive(
            {
             _statusName: '',
             _statusDate: '',
             _Status:''
            }       
        )
        let status_category = ref([])
        const search = ref('')
        let isAdmin = ref(localStorage.getItem("Userrole"))
        const  header_ = ref([
          {text: 'Service status', value: 'statusname', sortable: true},
          {text: 'Date', value: 'createdDate', sortable: true},
          {text: 'Action', value: 'actions', sortable: true, width: '180px'},
          ]

        )

        function setDate()
        {
            let currentDate = new Date();
            statusData._statusDate = currentDate.toLocaleDateString("en-US")
        }

        function status_fetch(){
           
              let apiURL = 'api/masterAPI/getServicestatus';
              axios.get(apiURL).then(res => {

                  // for (let i = 0; i < res.data.length; i++) {
                    status_category.value = res.data
                    // }
              }).catch(error => {
                console.log(error)
             });   
        }

        function saveServicestatus()
        {
            if(statusData._statusName !== '')
            {
                const ServicestatusData = {
                statusname :statusData._statusName,
                status :0,
                createdDate:statusData._statusDate
,
          }
          let apiURL = 'api/masterAPI/create-Servicestatus';
        axios.post(apiURL, ServicestatusData).then(res => {
          if(res.data)
          {
            // console.log(res.data)
            openedPanel.value = ''
            status_fetch()
            FormReset()
          } 
        }).catch(error => {
          if(error.request.status == 500)
          {
                snackbar.value = true
                color.value = "error"
                msg.value = "status name already exist !"
          }
        });
            }else{

                snackbar.value = true
                color.value = "error"
                msg.value = "Enter all details"

            }
       
      
        }
        let edited = ref(false)
        function editupload()
        {
         let updatectaeOBJ = {
          statusname :statusData._statusName,
          status:statusData._Status ,
          createdDate:statusData._statusDate,
          _id:E_id.value

         }
        //  console.log('aa'+E_id.value)
          let apiURL = `api/masterAPI/update-Servicestatus`;
          axios.patch(apiURL, updatectaeOBJ).then(res => {
          if(res.data)
          {
            openedPanel.value = ''
            edited.value  = false
            title.value = "Add Service status"
            status_fetch()
            FormReset()
            
          } 
          }).catch(error => {
            if(error.request.status == 500)
          {
                snackbar.value = true
                color.value = "error"
                msg.value = "Something went wrong !"
          }
        //    console.log(error)
          });
        }

        function editcategory_(item)
        {
            console.time(item)
            title.value = "Edit Service status"
            openedPanel.value = 0
            edited.value = true;
            statusData._statusName = item.statusname
            statusData._Status = item.status,
            statusData._statusDate = item.createdDate
            E_id.value = item._id
            console.timeEnd()
        }
        
        function FormReset()
        {
          title.value = "Add Service status"
          statusData._statusName ='';
          statusData._Status = 0;
           edited.value = false
        }
        function deleteServiceCategory(id)
        {
           this.$swal({
                      title: 'Are you sure? ',
                      text: "You will not be able to go back",
                      icon: 'warning',
                      showCancelButton: true,
                      confirmButtonColor: '#00b894',
                      cancelButtonColor: '#d63031',
                      confirmButtonText: 'Yes',
                      cancelButtonText: 'No',
                      }).then((result) => {
                          // Send request to the server
                           if (result.value) {
                                 try {
                  let apiURL = `api/masterAPI/delete-Servicestatus/${id}`;
                  let indexOfArrayItem = this.status_category.findIndex(i => i._id === id);
                  axios.delete(apiURL).then(() => {
                          this.status_category.splice(indexOfArrayItem, 1);
                      }).catch(error => {
                          console.log(error)
                      });
                  //  this.$swal(
                  //          'Deleted!',
                  //          'User has been deleted.',
                  //          'success'
                  //         )
                          //Notification error
                  this.color = 'error'
                  this.msg = "Data has been deleted."
                  this.snackbar = true;
                  //Notification error
              }catch (error) {
               this.$swal("Failed!", "There was something wrong."+error, "warning");
              }
                           }
                      })
    
              }
        
        return {
         title,
         statusData,
         _statusName,
         _statusDate,
         setDate,
         header_,
         status_fetch,
         status_category,
         search,
         saveServicestatus,
         edited,
         editupload,
         FormReset,
         isAdmin,
         status_,
         editcategory_,
         openedPanel,
         _Status,
         E_id,
         deleteServiceCategory,
         snackbar,
         color,
         msg,
        }
    }
   
})
</script>
